
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

.App {
  background-color: #f8c950;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
}


.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu li {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}
.menu-item {
  background-color: white;
  border-radius:320px;
  text-decoration: none;
  color: black;
  padding: 10px 20px;
  box-shadow: 0 4px 0px rgba(0, 0, 0, 22);
  font-family: 'Montserrat', sans-serif;
}

.menu-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
.logo-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 10px 0;
}

.logo {
  width: 100px;
  height: auto;
}

.social-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 10px 0;
}

.social-icon {
  width: 40px;
  height: auto;
  cursor: pointer;
}
.logo-1 {
  width: 150px;
  height: auto;
}
.logo-2 {
  width: 600px;
  height: auto;
}
.nft-collection {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  align-items: center;
  justify-items: center;
  padding: 20px;
  margin-bottom: 500px;

}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  font-family: 'Open Sans', sans-serif;
}
.cards-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-items: center;
  margin-top: 20px;
  margin-bottom: 500px;
}
.site-footer {
  position: fixed;
  left: 0;
  bottom: -10px;
  width: 100%;
  text-align: center;
  background-color: #3a290000;

}

.site-footer img {
  width: 750px;
  height: auto;
}
.story-container {
  margin-bottom: 200px;
  padding-bottom: 20px;
}
.artists-container{
  margin-bottom: 200px;
  padding-bottom: 20px;
}
.sign-in-container {
  position: absolute;
  top: 20px;
  right: 20px;
}

.sign-in-button {
  background-color: #6f42c1;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.sign-in-button:hover {
  background-color: #5e3cb9;
}
